import React, { useState } from 'react';
import Step1 from './Step1';
import Step2 from './Step2';
import Step3 from './Step3';

const MatchForm = () => {
    const [step, setStep] = useState(1);
    const [formData, setFormData] = useState({
        preferredGender: '',
        ageRange: '',
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const nextStep = () => {
        setStep(step + 1);
    };

    const prevStep = () => {
        setStep(step - 1);
    };

    const handleSubmit = () => {
        // handle form submission
        console.log('Form submitted:', formData);
    };

    switch (step) {
        case 1:
            return <Step1 formData={formData} handleChange={handleChange} nextStep={nextStep} />;
        case 2:
            return <Step2 formData={formData} handleChange={handleChange} nextStep={nextStep} prevStep={prevStep} />;
        case 3:
            return <Step3 formData={formData} prevStep={prevStep} handleSubmit={handleSubmit} />;
        default:
            return <Step1 formData={formData} handleChange={handleChange} nextStep={nextStep} />;
    }
};

export default MatchForm;
